.switcher {
    /* position: absolute; */
    display: flex;
    border-radius: 27px;
    height: 34px;
    font-size: 0.5em;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin: 0 5px;
}

.container-unit {
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 5px; */
    width: 34px;
    height: 34px;
    border-radius: 17px;
    color: rgb(255, 255, 255);
}

.green {
    background: rgb(99, 194, 130);
}

.blue {
    background: #2196F3;
}


/* Hide default HTML checkbox */

.switch input {
    display: none;
}


/* The slider */

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(99, 194, 130);
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #2196F3;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}